define(['lodash', 'app/vobjects/vobject'], (_, vobject) => {
  class ADSR extends vobject.VObject {
    numInputs() { return 1; }
    numOutputs() { return 1; }

    constructor(...args) {
      super(...args);
      [this.attack, this.decay, this.sustain, this.release] = args;
    }
  }

  return ADSR;
});
